import React, { useState } from 'react'
import Button from "@material-ui/core/Button"
import Dialog from "@material-ui/core/Dialog"
import DialogTitle from "@material-ui/core/DialogTitle"
import DialogActions from "@material-ui/core/DialogActions"
import DialogContent from "@material-ui/core/DialogContent"
import TextField from "@material-ui/core/TextField"
import FormControl from '@material-ui/core/FormControl'
import MenuItem from '@material-ui/core/MenuItem'
import InputLabel from '@material-ui/core/InputLabel'
import Select from '@material-ui/core/Select'
import { MuiPickersUtilsProvider, DatePicker } from 'material-ui-pickers'
import DateFnsUtils from '@date-io/date-fns'
import { mutationGraphql } from '@/component/ApolloQuery'

export const initState = () => {
  const [formData, setFormData] = useState({})
  return {
    setFormData, formData,
  }
}

export const SearchForm = (
    {
      setFormData,
      formData,
      formColumn,
    }) => {
  return (
      <form>
        {
          formColumn.map(item => {
            const [name] = item
            return (
                <FormControl key={`SearchForm${name}`}>
                  {
                    getFormItem({
                      ...item,
                      formData,
                      setFormData,
                    })
                  }
                </FormControl>
            );
          })
        }
      </form>
  )
}

const getFormItem = (option) => {
  const { name, type, formData, setEdit, options = [] } = option

  if (type === 'Select') {
    return <>
      <InputLabel shrink={formData[name]}>{name}</InputLabel>
      <Select
          value={formData[name]}
          onChange={e => setEdit({
            [name]: e.target.value
          })}
      >
        {
          options.map(([value, text]) => <MenuItem key={value}
                                                   value={value}>{text}</MenuItem>)
        }
      </Select>
    </>
  }
  if (type === 'DatePicker') {
    return <MuiPickersUtilsProvider utils={DateFnsUtils}>
      <DatePicker
          label={name}
          value={formData[name]}
          onChange={e => {
            return setEdit({
              [name]: e
            })
          }}
      />
    </MuiPickersUtilsProvider>
  }
  return <TextField
      label={name}
      value={formData[name] || ''}
      multiline={option.multiline}
      rows={option.rows}
      onChange={e => setEdit({
        [name]: e.target.value
      })}
  />

}
